<template>
    <div id="app">
        <!-- 메인 제목: 과일 선택 -->
        <h1>과일 선택</h1>

        <!-- 체크박스 리스트 영역 -->
        <!-- fruits 배열의 각 항목을 반복하여 체크박스를 생성 -->
        <div class="checkbox-container" v-for="fruit in fruits" :key="fruit.id">
            <!-- 체크박스와 레이블을 포함한 구조 -->
            <label class="checkbox-label">
                <!-- 체크박스 input -->
                <!-- :value로 fruit.name 설정, v-model로 selectedFruits에 바인딩 -->
                <input type="checkbox" :value="fruit.name" v-model="selectedFruits" class="custom-checkbox" />
                <!-- 체크박스의 커스텀 디자인을 위한 영역 -->
                <span class="checkbox-custom"></span>
                <!-- 체크박스 옆에 표시되는 과일 이름 -->
                {{ fruit.name }}
            </label>
        </div>

        <!-- 선택된 과일 출력 영역 -->
        <h2>선택된 과일:</h2>
        <div class="selected-container">
            <!-- 선택된 과일이 없을 때 표시되는 메시지 -->
            <p v-if="selectedFruits.length === 0">아직 선택된 과일이 없습니다.</p>
            <!-- 선택된 과일이 있을 때 출력하는 리스트 -->
            <ul v-else>
                <li v-for="fruit in selectedFruits" :key="fruit">
                    <!-- 선택된 과일 이름 출력 -->
                    {{ fruit }}
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: "App", // 컴포넌트 이름
        data() {
            return {
                // 체크박스에 표시할 과일 목록 배열
                // 각 항목은 id와 name 속성을 포함
                fruits: [{
                        id: 1,
                        name: "사과"
                    },
                    {
                        id: 2,
                        name: "바나나"
                    },
                    {
                        id: 3,
                        name: "포도"
                    },
                    {
                        id: 4,
                        name: "오렌지"
                    },
                    {
                        id: 5,
                        name: "딸기"
                    }
                ],
                // 선택된 과일을 저장할 배열
                // 사용자가 체크박스를 클릭할 때 업데이트됨
                selectedFruits: []
            };
        }
    };
</script>

<style>
    * {
        margin: 0;
        padding: 0;
    }

    #app {
        font-family: 'Nanum Gothic', Arial, sans-serif;
        text-align: center;
        padding: 20px;
        background: linear-gradient(135deg, #ffecd2 0%, #fcb69f 100%);
        color: #444;
        min-height: 100vh;
    }


    h1 {
        font-size: 2.5rem;
        margin-bottom: 20px;
        color: #ff6f61;
        text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
    }

    h2 {
        margin-top: 30px;
        font-size: 1.8rem;
        color: #333;
    }


    .checkbox-container {
        margin: 10px 0;
    }


    .checkbox-label {
        margin: 0 auto;
        width: 450px;
        display: flex;
        align-items: center;
        font-size: 1.2rem;
        cursor: pointer;
        user-select: none;
        position: relative;
    }


    .custom-checkbox {
        display: none;
    }


    .checkbox-custom {
        width: 20px;
        height: 20px;
        border: 2px solid #ff6f61;
        border-radius: 4px;
        margin-right: 10px;
        background-color: #fff;
        transition: all 0.3s ease;
    }


    .custom-checkbox:checked+.checkbox-custom {
        background-color: #ff6f61;
        border-color: #ff6f61;
        box-shadow: 0 0 8px rgba(255, 111, 97, 0.5);
    }

    .selected-container {
        background: #fff;
        padding: 20px;
        margin: 20px auto;
        border-radius: 10px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        max-width: 400px;
    }

    .selected-container ul {
        list-style: none;
        padding: 0;
    }

    .selected-container li {
        margin: 5px 0;
        font-size: 1.2rem;
        color: #444;
    }
</style>